html {
    height: 100%;
    width: 100%;
}

.first {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;


    .firstRow {
        display: flex;
        flex-direction: row;
        max-width: 1500px;
        width: 100%;
        position: relative;
        justify-content: space-around;
        align-content: flex-start;
        height: auto;
        top: 155px;
        z-index: 0;

        .left {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: flex-start;
            width: 50%;
            height: 800px;
            margin: 0px 15px;
            z-index: 5;

            h1 {
                color: white;

                font-size: 50px;

                span {
                    font-family: Sofia, sansSerif;
                    font-style: oblique;
                }
            }

            .Input {
                width: 600px;
                height: 50px;
                margin-top: 0px;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: flex-start;
                position: relative;
                bottom: 20%;

                .firstRow-input {
                    width: 80%;
                    font-size: 20px;
                }

                .glass {
                    position: relative;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    width: 40px;
                    height: 40px;
                    background-color: green;
                }

                .search {
                    position: relative;
                    display: none;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    width: 40px;
                    height: 40px;
                    background-color: green;
                }
            }

            .spann {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: flex-start;
                width: 780px;
                position: relative;
                height: 50px;
                bottom: 40%;

                h5 {
                    color: white;
                    font-size: 25px;
                    margin-left: 10px;
                    margin-right: 10px;
                    width: auto;




                }

                span {
                    color: white;
                    cursor: pointer;

                    &:nth-child(2) {
                        border: 2px solid white;
                        width: 145px !important;
                        font-size: 15px;
                        padding: 3px 10px;
                        margin-left: 10px;
                        margin-right: 10px;
                        margin-bottom: 220px;
                        border-radius: 23px;
                    }

                    &:nth-child(3) {
                        border: 2px solid white;
                        width: 100px !important;
                        font-size: 15px;
                        padding: 3px 10px;
                        margin-left: 10px;
                        margin-right: 10px;
                        margin-bottom: 220px;
                        border-radius: 23px;
                    }

                    &:nth-child(4) {
                        border: 2px solid white;
                        width: 118px !important;
                        font-size: 15px;
                        padding: 3px 10px;
                        margin-left: 10px;
                        margin-right: 10px;
                        margin-bottom: 220px;
                        border-radius: 23px;
                    }

                    &:nth-child(5) {
                        border: 2px solid white;
                        width: 110px !important;
                        font-size: 15px;
                        padding: 3px 10px;
                        margin-left: 10px;
                        margin-right: 10px;
                        margin-bottom: 220px;
                        border-radius: 23px;

                    }

                    &:hover {
                        background-color: white;
                        color: black;
                        transition: 0.5s;
                    }


                }

            }
        }

        .right {
            margin: 0px 15px;
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;
            width: 50%;
            height: 100%;
            z-index: 0;

            img {
                width: 550px;
                height: 550px;
            }
        }
    }
}


.secondRow {
    margin: 50px 0px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    div {
        display: flex;
        align-items: center;
        justify-content: center;
        align-items: center;
        position: relative;

        img {
            margin: 0px 35px;
            width: 90px;
            height: 65px;
        }
    }
}

.third {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    margin: auto;

    .wrapper {
        background-color: #fff;
        max-width: 1500px;
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        height: auto;
        margin: 50px 0px;

        i {
            top: 50%;
            height: 50px;
            width: 50px;
            cursor: pointer;
            font-size: 1.25rem;
            position: absolute;
            text-align: center;
            line-height: 50px;
            background: #fff;
            border-radius: 50%;
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.23);
            transform: translateY(-50%);
            transition: transform 0.1s linear;
            z-index: 1;

            &:active {
                transform: translateY(-50%) scale(0.85);
            }

            &:first-child {
                left: 0px;
            }

            &:last-child {
                right: 0px;
            }

        }

        .carousel {
            display: grid;
            grid-auto-flow: column;
            grid-auto-columns: calc((100% / 5) - 12px);
            width: 100%;

            overflow-x: auto;
            scroll-snap-type: x mandatory;
            gap: 16px;
            border-radius: 8px;
            scroll-behavior: smooth;


            &::-webkit-scrollbar {
                display: none;
            }

            &.no-transition {
                scroll-behavior: auto;
            }

            &.dragging {
                z-index: 20;
                scroll-snap-type: none;
                scroll-behavior: auto;

                .card {
                    cursor: grab !important;

                    z-index: 15;
                    user-select: none !important;

                }
            }

            :where(.card, .img) {
                display: flex !important;

                justify-content: center !important;

                align-items: center !important;

            }

            .card {
                scroll-snap-align: start !important;

                height: 370px !important;

                list-style: none !important;

                background: #fff !important;

                cursor: pointer !important;

                padding-bottom: 15px !important;

                flex-direction: column !important;

                border-radius: 8px !important;




                .img {

                    height: 550px !important;

                    width: 350px !important;



                    position: relative;

                }

                .intro {

                    position: absolute;
                    bottom: 265px;
                    color: white;
                    width: 100%;
                    padding: 0px 15px;

                    h2 {
                        font-weight: 500;
                        font-size: 1.56rem;
                        margin: 30px 0 5px;
                    }

                    span {
                        color: #6A6D78;
                        font-size: 1.31rem;
                    }
                }

            }
        }
    }
}





