.modal {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    position: fixed;
    display: flex;
    top: 0px;
    right: 0px;
    z-Index: 99;
    justify-content: center;
    align-items: center;

    .Video {
        width: 80%;
        height: 100%;
        border-radius: 10px 0 0 10px;
        position: relative;
        left: 100px;
        text-align: center;
        background: #000;
    }
}

@media screen and (max-width: 600px) {
    .modal {
        .Video {
            left: 40px;
        }
    }
}